


let daily = require('./draw.js').daily

export default {
data () {
return {
time:{
month:Number,
date:Number,
hours:Number,
},
restHour:24-(new Date).getHours(),
msg:"Choose one",
startTouchTime:0,
draw:false,
path:'',
mean:'',
card:'',
style:'',
google:'https://google.com/search?q='
}
},
created(){
let rc = this.$ls.get('daily','nothing')
if(rc=='nothing') return;

this.render(rc);
},
methods:{
render(d){
if(d.reversed) this.style='transform: scaleY(-1);'
this.path = d.path;
this.mean = d.mean;
this.card = d.card;
this.time = d.time;
this.msg="Go Back"
this.draw = true;

},
tStart(e){
this.startTouchTime = e.timeStamp;
this.size+=50;
},
tEnd(e){
let d = daily();
let now = new Date;
d.time = {
'month':now.getMonth()+1,//會差一個月
'date':now.getDate(),
'hours':now.getHours()
};
let h = (24 - now.getHours())
let m = (60- now.getMinutes())

let restTime = ((h-1)*60 + m)*60 *1000; 

this.render(d);
this.$ls.set(
'daily',{
'time':this.time,
'card':d.card,
'path':d.path,
'reversed':d.reversed,
'mean':d.mean
},restTime);//a day expire
}
}
}



