export default {
  name: 'app',
  mounted(){
    if(innerWidth>innerHeight){
      //console.log('computer')
      document.getElementById('app').style.width="50vw";
    }

  },
  methods:{
    goBack(){
      if(this.$route.path==='/') return;
      history.go(-1);
    }
  }
}



